.footer {
  background-color: #091828;
  position: fixed;
  width: 100%;
  bottom: 0;
}

.footer img {
  width: 20px;
  height: 20px;
  margin: 5px;
}

.footer span {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 12px;
  color: #61dafb;
}

.footer a {
  color: burlywood;
}
